import React, { useState,useRef } from 'react';
import { signUp } from '../../api/AuthApi'; 
import { useNavigate } from "react-router-dom";
import { 
  CModal, CModalBody, CCloseButton, 
  CButton, CToast, CToastBody, 
  CToaster, CToastHeader, CModalFooter
} from '@coreui/react';

const RegisterModal = ({ visible, toggle }) => {
  const [formData, setFormData] = useState({
    username: '',
    email: '',
    password: '',
    confirmPassword: '',
    displayName: '',
    phoneNumber: '',
    birthday: '',
    region: '',
    identityCard: '',
    university: '',
    major: '',
    year: '',
    studentId: '',
    linkFb: '',
    otherUniversity: '',
  });
  const navigate = useNavigate();
  const [toast, setToast] = useState(null);
  const toaster = useRef();
  const [selectedUniversity, setSelectedUniversity] = useState('');

  const handleChange = (e) => {
    const { id, value } = e.target;
    setFormData({
      ...formData,
      [id]: value
    });
  };

  const handleUniversityChange = (e) => {
    setSelectedUniversity(e.target.value);
    setFormData({
      ...formData,
      university: e.target.value === 'other' ? '' : e.target.selectedOptions[0].innerText,
    });
    console.log(e.target.selectedOptions[0].innerText)
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log('Form data:', formData);
    
    try {
      const response = await signUp(
        formData.username,
        formData.email,
        formData.password,
        formData.displayName,
        formData.phoneNumber,
        formData.birthday,
        formData.university !=''?formData.university:formData.otherUniversity,
        formData.major,
        formData.year,
        formData.studentId,
        formData.linkFb
      );
      if (response.status==200 && response.data.accessToken!=null) {
        const { data, accessToken } = response.data;
        localStorage.setItem("accessToken", accessToken);
        localStorage.setItem("userInfo", JSON.stringify(data));
        setToast(
          <CToast autohide={true} visible={true} color="success">
            <CToastHeader closeButton>
              <svg className="rounded me-2" width="20" height="20" xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid slice" focusable="false" role="img">
                <rect width="100%" height="100%" fill="#28a745"></rect>
              </svg>
              <div className="fw-bold me-auto">Thng bo</div>
            </CToastHeader>
            <CToastBody>ng k thnh cng!</CToastBody>
          </CToast>
        );
        navigate("/welcome");
      }

    } catch (error) {
      if (error.response && error.response.status === 400 && error.response.data.msg === "User already exists") {
        setToast(
          <CToast autohide={true} visible={true} color="danger">
            <CToastHeader closeButton>
              <svg className="rounded me-2" width="20" height="20" xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid slice" focusable="false" role="img">
                <rect width="100%" height="100%" fill="#dc3545"></rect>
              </svg>
              <div className="fw-bold me-auto">Li</div>
            </CToastHeader>
            <CToastBody>S in thoi hoc Email  tn ti</CToastBody>
          </CToast>
        );
      } else {
        setToast(
          <CToast autohide={true} visible={true} color="danger">
            <CToastHeader closeButton>
              <svg className="rounded me-2" width="20" height="20" xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid slice" focusable="false" role="img">
                <rect width="100%" height="100%" fill="#dc3545"></rect>
              </svg>
              <div className="fw-bold me-auto">Li</div>
            </CToastHeader>
            <CToastBody>ng k tht bi. Vui lng kim tra thng tin v th li.</CToastBody>
          </CToast>
        );
      }
    }
  };

  return (
    <CModal
      alignment="center"
      visible={visible}
      onClose={toggle}
      aria-labelledby="VerticallyCenteredExample"
      size="lg"
      className="custom-modal"
    >
      <CModalBody className="text-white modal-body modal-content">
        <div className="d-flex justify-content-end custom-close-button">
          <CCloseButton onClick={toggle} />
        </div>
        <h2 className="text-center" style={{ marginBottom: "30px" }}>ng K</h2>
        <form id="signUpForm" onSubmit={handleSubmit}>
          <div className="form-row">
            <div className="form-group col-md-6">
              <label htmlFor="username">Tn ng nhp</label>
              <input
                type="text"
                className="form-control"
                id="username"
                placeholder="Tn ng nhp"
                value={formData.username}
                onChange={handleChange}
                required
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="email">Email</label>
              <input
                type="email"
                className="form-control"
                id="email"
                placeholder="Email"
                value={formData.email}
                onChange={handleChange}
                required
              />
            </div>
          </div>
          <div className="form-row">
            <div className="form-group col-md-6">
              <label htmlFor="password">Mt khu</label>
              <input
                type="password"
                className="form-control"
                id="password"
                placeholder="Mt khu"
                value={formData.password}
                onChange={handleChange}
                required
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="confirmPassword">Nhp li mt khu</label>
              <input
                type="password"
                className="form-control"
                id="confirmPassword"
                placeholder="Nhp li mt khu"
                value={formData.confirmPassword}
                onChange={handleChange}
                required
              />
            </div>
          </div>
          <div className="form-row">
            <div className="form-group col-md-6">
              <label htmlFor="displayName">H v tn</label>
              <input
                type="text"
                className="form-control"
                id="displayName"
                placeholder="H v tn"
                value={formData.displayName}
                onChange={handleChange}
                required
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="phoneNumber">S in thoi</label>
              <input
                type="text"
                className="form-control"
                id="phoneNumber"
                placeholder="S in thoi"
                value={formData.phoneNumber}
                onChange={handleChange}
                required
              />
            </div>
          </div>
          <div className="form-row">
            <div className="form-group col-md-6">
              <label htmlFor="birthday">Ngy sinh</label>
              <input
                type="date"
                className="form-control"
                id="birthday"
                value={formData.birthday}
                onChange={handleChange}
                required
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="region">Khu vc</label>
              <input
                type="text"
                className="form-control"
                id="region"
                placeholder="Khu vc"
                value={formData.region}
                onChange={handleChange}
                required
              />
            </div>
          </div>
          <div className="form-row">
            <div className="form-group col-md-6">
              <label htmlFor="identityCard">Cn cc cng dn</label>
              <input
                type="text"
                className="form-control"
                id="identityCard"
                placeholder="Cn cc cng dn"
                value={formData.identityCard}
                onChange={handleChange}
                required
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="university">Bn l sinh vin trng no?</label>
              <select
                className="form-control"
                id="university"
                value={selectedUniversity}
                onChange={handleUniversityChange}
                required
              >
                 <option value="">Chn trng...</option>
                <option value="BA">BA - Hc vin Ngn hng</option>
                <option value="FTU">FTU - i hc Ngoi thng</option>
                <option value="NEU">NEU - i hc Kinh t Quc Dn</option>
                <option value="AOF">AOF - Hc vin Ti chnh</option>
                <option value="UEB">UEB - Trng i hc Kinh t - i hc Quc gia H Ni</option>
                <option value="TMU">TMU - i hc Thng mi</option>
                <option value="FTU2">FTU2 - Trng i hc Ngoi Thng c s II</option>
                <option value="UEH">UEH - Trng i hc Kinh t TP.HCM</option>
                <option value="HUB">HUB - Trng i hc Ngn hng TP.HCM</option>
                <option value="UEL">UEL - Trng i hc Kinh t - Lut - i hc Quc gia TP.HCM</option>
                <option value="UEF">UEF - Trng i hc Kinh t - Ti chnh TP.HCM</option>
                <option value="RMIT">RMIT - Trng i hc RMIT</option>
                <option value="other">Trng khc</option>
              </select>
            </div>
            {selectedUniversity === 'other' && (
            <div className="form-group col-md-6">
              <label htmlFor="otherUniversity">Tn trng khc</label>
              <input
                type="text"
                className="form-control"
                id="otherUniversity"
                placeholder="in tn trng"
                value={formData.otherUniversity}
                onChange={handleChange}
                required
              />
            </div>
          )}
          </div>
          <div className="form-row">
            <div className="form-group col-md-6">
              <label htmlFor="major">Chuyn ngnh</label>
              <input
                type="text"
                className="form-control"
                id="major"
                placeholder="Chuyn ngnh"
                value={formData.major}
                onChange={handleChange}
                required
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="year">Nm hc</label>
              <input
                type="number"
                className="form-control"
                id="year"
                placeholder="Sinh vin nm my"
                value={formData.year}
                onChange={handleChange}
                required
              />
            </div>
          </div>
          <div className="form-row">
            <div className="form-group col-md-6">
              <label htmlFor="studentId">M sinh vin</label>
              <input
                type="text"
                className="form-control"
                id="studentId"
                placeholder="in m sinh vin nu l sinh vin Hc vin Ngn hng"
                value={formData.studentId}
                onChange={handleChange}
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="linkFb">Link Facebook</label>
              <input
                type="text"
                className="form-control"
                id="linkFb"
                placeholder="Link Facebook"
                value={formData.linkFb}
                onChange={handleChange}
              />
            </div>
          </div>
          <div className="login-link-container">
            <span> c ti khon? <a href="/login">ng nhp</a></span>
          </div>
          <div className='custom-button-container'>
            <button type="submit" className="btn btn-light btn-block custom-register">ng k ngay</button>
          </div>
        </form>
      </CModalBody>
      <CModalFooter>
        <CToaster placement="top-end" push={toast} ref={toaster} />
      </CModalFooter>
    </CModal>
  );
};

export default RegisterModal;
